.game-board {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 450px;
	height: 550px;
}

.status {
	font-size: 125%;
	margin-bottom: 20px;
}

.restart {
	margin-top: 30px;
	border: none;
	background: none;
	font-size: 125%;
	color: white;
	padding: 10px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.restart:hover {
	/*border: 2px solid #209f54;*/
	background-color: #209f54;
	color: #58de8f;
	cursor: pointer;
}

.restart:focus {
	outline: none;
}


